<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="info" center show-icon :closable="false"></el-alert>
      <!--  -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!-- tabs -->
      <el-form :model="addGoodsForm" :rules="addGoodsRules" ref="addGoodsRef" label-width="100px" label-position="top">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name"><el-input v-model="addGoodsForm.goods_name"></el-input></el-form-item>
            <el-form-item label="" prop="goods_price"><el-input type="number" v-model="addGoodsForm.goods_price"></el-input></el-form-item>
            <el-form-item label="" prop="goods_number"><el-input type="number" v-model="addGoodsForm.goods_number"></el-input></el-form-item>
            <el-form-item label="" prop="goods_weight"><el-input type="number" v-model="addGoodsForm.goods_weight"></el-input></el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader expand-trigger="hover" :options="cateList" v-model="addGoodsForm.goods_cat" :props="cateProps" @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item v-for="(item, i) in manyListData" :label="item.attr_name" :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals"><el-checkbox border :label="cb" v-for="(cb, i) in item.attr_vals" :key="i"></el-checkbox></el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyListData" :key="item.attr_id"><el-input v-model="item.attr_vals"></el-input></el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload :on-success="handleSuccess" :action="updateUrl" :headers="headers" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture">
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor ref="goodsRef" v-model="addGoodsForm.goods_introduce" />
            <!--  -->
            <el-button type="primary" class="goods-btn" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="imgDialogVisible" width="50%"><img :src="tempPath" alt="" class="img" /></el-dialog>
  </div>
</template>
<script>
import _ from 'lodash';
export default {
  data() {
    return {
      //
      activeIndex: '0',
      //
      addGoodsForm: {
        //
        goods_name: '',
        //
        goods_price: 1,
        //
        goods_number: 1,
        //
        goods_weight: 1,
        //
        goods_cat: [],
        //
        pics: [],
        //
        goods_introduce: '',
        //
        attrs: []
      },
      //
      addGoodsRules: {
        goods_name: [{ required: true, message: '', trigger: 'blur' }],
        goods_price: [{ required: true, message: '', trigger: 'blur' }],
        goods_number: [{ required: true, message: '', trigger: 'blur' }],
        goods_weight: [{ required: true, message: '', trigger: 'blur' }],
        goods_cat: [{ required: true, message: '', trigger: 'blur' }]
      },
      //
      cateList: [],
      //
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      manyListData: [],
      //
      onlyListData: [],
      //URL
      updateUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      //
      headers: {
        Authorization: window.sessionStorage.getItem('token')
      },
      //
      imgDialogVisible: false,
      //
      tempPath: ''
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get('categories');
      if (res.meta.status !== 200) return this.$message.error('');
      this.cateList = res.data;
    },
    //
    handleChange() {
      if (this.addGoodsForm.goods_cat.length !== 3) {
        this.addGoodsForm.goods_cat = [];
      }
    },
    //tabs
    beforeTabLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addGoodsForm.goods_cat.length !== 3) {
        this.$message.error('');
        return false;
      }
    },
    //tab
    async tabClicked() {
      if (this.activeIndex === '1') {
        //
        const { data: res } = await this.$http.get(`categories/${this.getCateId}/attributes`, { params: { sel: 'many' } });
        if (res.meta.status !== 200) return this.$message.error('');
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(',');
        });
        this.manyListData = res.data;
      } else if (this.activeIndex === '2') {
        //
        const { data: res } = await this.$http.get(`categories/${this.getCateId}/attributes`, { params: { sel: 'only' } });
        if (res.meta.status !== 200) return this.$message.error('');

        this.onlyListData = res.data;
      }
    },
    //
    handlePreview(file) {
      this.tempPath = file.response.data.url;
      //
      this.imgDialogVisible = true;
    },
    //
    handleRemove(file) {
      const filePath = file.response.data.tmp_path;
      //
      const i = this.addGoodsForm.pics.findIndex(x => x.pic === filePath);
      //
      this.addGoodsForm.pics.splice(i, 1);
    },
    //
    handleSuccess(response) {
      //
      const imagObj = { pic: response.data.tmp_path };
      this.addGoodsForm.pics.push(imagObj);
    },
    //
    addGoods() {
      this.$refs.addGoodsRef.validate(async valid => {
        if (!valid) return;
        //
        this.manyListData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join(',') };
          this.addGoodsForm.attrs.push(newInfo);
        });
        //
        this.onlyListData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals };
          this.addGoodsForm.attrs.push(newInfo);
        });
        //
        const form = _.cloneDeep(this.addGoodsForm);
        form.goods_cat = form.goods_cat.join(',');
        const { data: res } = await this.$http.post('goods', form);
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg);
        this.$message.success('');
        //
        this.$router.push('/goods');
      });
    }
  },
  computed: {
    getCateId() {
      if (this.addGoodsForm.goods_cat.length === 3) {
        return this.addGoodsForm.goods_cat[2];
      }
      return null;
    }
  }
};
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
.img {
  width: 100%;
}
.goods-btn {
  margin: 10px;
}
</style>
