<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title="" type="warning" :closable="false"></el-alert>
      <!--  -->
      <el-row class="cate">
        <el-col>
          <span></span>
          <el-cascader v-model="cateValue" :options="cateList" :props="cateProps" @change="cateHandleChange" expand-trigger="hover"></el-cascader>
        </el-col>
      </el-row>

      <!-- Tabs -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="many">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addParamsDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="manyParamsList" border style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag @close="handleClose(i, scope.row)" closable v-for="(item, i) in scope.row.attr_vals" :key="i">{{ item }}</el-tag>
                <!-- tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!-- tag -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="attr_id" label="ID"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editParams(scope.row)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteParams(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addParamsDialogVisible = true"></el-button>
          <!--  -->
          <el-table :data="onlyParamsList" border style="width: 100%">
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <!-- tag -->
                <el-tag @close="handleClose(i, scope.row)" closable v-for="(item, i) in scope.row.attr_vals" :key="i">{{ item }}</el-tag>
                <!-- tag -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                ></el-input>
                <!-- tag -->
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="attr_id" label="ID"></el-table-column>
            <el-table-column prop="attr_name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="editParams(scope.row)"></el-button>
                <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteParams(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>

    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="addParamsDialogVisible" width="50%" @close="addParamsClose">
      <el-form :model="addParamsForm" :rules="addParamsRules" ref="addParamsRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name"><el-input v-model="addParamsForm.attr_name"></el-input></el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addParamsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="editParamsDialogVisible" width="50%" @close="editParamsClose">
      <el-form :model="editParamsForm" :rules="editParamsRules" ref="editParamsRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name"><el-input v-model="editParamsForm.attr_name"></el-input></el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editParamsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="subEditParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //ID
      cateValue: [],
      //
      cateList: [],
      //
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //tabspane
      activeName: 'many',
      //
      manyParamsList: [],
      //
      onlyParamsList: [],
      //
      addParamsDialogVisible: false,
      //
      addParamsForm: {},
      addParamsRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editParamsDialogVisible: false,
      // form
      editParamsForm: {},
      //
      editParamsRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      //tag
      inputVisible: false,
      //tag
      inputValue: ''
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get('categories');
      if (res.meta.status !== 200) return this.$message.error('');
      this.cateList = res.data;
    },
    //
    cateHandleChange() {
      this.getParamsData();
    },
    //tabs
    handleClick() {
      this.getParamsData();
    },
    //
    async getParamsData() {
      //
      if (this.cateValue.length !== 3) {
        this.cateValue = [];
        this.manyParamsList = [];
        this.onlyParamsList = [];
        return false;
      }
      ///
      const { data: res } = await this.$http.get(`categories/${this.getCateId}/attributes`, { params: { sel: this.activeName } });
      if (res.meta.status !== 200) return this.$message.error('');
      // 
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(',') : [];
        //tag
        item.inputVisible = false;
        item.inputValue = '';
      });
      if (this.activeName === 'many') {
        this.manyParamsList = res.data;
      } else {
        this.onlyParamsList = res.data;
      }
    },
    //
    addParams() {
      //
      this.$refs.addParamsRef.validate(async valid => {
        const { data: res } = await this.$http.post(`categories/${this.getCateId}/attributes`, {
          attr_name: this.addParamsForm.attr_name,
          attr_sel: this.activeName
        });
        if (res.meta.status !== 201) return this.$message.error('');
        this.$message.success('');
        //
        this.getParamsData();
        //
        this.addParamsDialogVisible = false;
      });
    },
    //
    addParamsClose() {
      //
      this.$refs.addParamsRef.resetFields();
    },
    //
    async editParams(info) {
      const { data: res } = await this.$http.get(`categories/${this.getCateId}/attributes/${info.attr_id}`, { params: { attr_sel: this.activeName } });
      if (res.meta.status !== 200) return this.$message.error('');
      this.editParamsForm = res.data;
      console.log(this.editParamsForm);
      this.getParamsData();
      //
      this.editParamsDialogVisible = true;
    },
    //
    subEditParams() {
      this.$refs.editParamsRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.put(`categories/${this.getCateId}/attributes/${this.editParamsForm.attr_id}`, {
          attr_name: this.editParamsForm.attr_name,
          attr_sel: this.activeName
        });
        if (res.meta.status !== 200) return this.$message.error('');
        this.$message.success('');
        //
        this.getParamsData();
        //
        this.editParamsDialogVisible = false;
      });
    },
    //
    editParamsClose() {
      this.$refs.editParamsRef.resetFields();
    },
    //
    deleteParams(info) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(`categories/${this.getCateId}/attributes/${info.attr_id}`);
          if (res.meta.status !== 200) {
            return this.$message({
              type: 'error',
              message: '!'
            });
          }

          this.$message({
            type: 'success',
            message: '!'
          });
          this.getParamsData();
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    //tag
    async handleInputConfirm(row) {
      //value
      if (row.inputValue.trim().length === 0) {
        row.inputValue = '';
        row.inputVisible = false;
        return false;
      }
      //tag
      row.attr_vals.push(row.inputValue.trim());
      //inputValue
      row.inputValue = '';
      //
      row.inputVisible = false;
      //
      this.saveAttrVals(row);
    },
    async saveAttrVals(row) {
      //
      const { data: res } = await this.$http.put(`categories/${this.getCateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: this.activeName,
        attr_vals: row.attr_vals.join(',')
      });
      if (res.meta.status !== 200) return this.$message.error('');
      this.$message.success('');
    },
    //tag
    handleClose(i, row) {
      //
      row.attr_vals.splice(i, 1);
      //
      this.saveAttrVals(row);
    },
    //tag
    showInput(row) {
      //
      row.inputVisible = true;
      //
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    }
  },
  computed: {
    //
    isBtnDisabled() {
      if (this.cateValue.length !== 3) {
        return true;
      }
      return false;
    },
    //ID
    getCateId() {
      if (this.cateValue.length === 3) {
        return this.cateValue[2];
      }
      return null;
    },
    //
    titleText() {
      if (this.activeName === 'many') {
        return '';
      }
      return '';
    }
  }
};
</script>

<style lang="less" scoped>
.cate {
  margin-top: 15px;
}
.el-cascader {
  width: 20%;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
