<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4"><el-button type="primary" @click="addGoods"></el-button></el-col>
      </el-row>

      <!--  -->
      <el-table :data="goodsList" border style="width: 100%">
        <el-table-column prop="goods_id" label="ID" width="90px"></el-table-column>
        <el-table-column prop="goods_name" label=""></el-table-column>
        <el-table-column prop="goods_price" label="()" width="90px"></el-table-column>
        <el-table-column prop="goods_weight" label="" width="90px"></el-table-column>
        <el-table-column prop="add_time" label="" width="200px">
          <template slot-scope="scope">
            {{ scope.row.add_time | date }}
          </template>
        </el-table-column>
        <el-table-column label="" width="300px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editGoods(scope.row)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteGoods(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 10, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      goodsList: [],
      //
      queryInfo: {
        //
        query: '',
        //
        pagenum: 1,
        //
        pagesize: 2
      },
      total: 0
    };
  },
  created() {
    this.getGoodsList();
  },
  methods: {
    //
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo });
      if (res.meta.status !== 200) return this.$message.error('');
      this.goodsList = res.data.goods;
      this.total = res.data.total;
      console.log(this.goodsList);
    },
    //
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.getGoodsList();
    },
    //
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.getGoodsList();
    },
    //ID
    deleteGoods(info) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.delete(`goods/${info.goods_id}`);
          if (res.meta.status !== 200) {
            return this.$message({
              type: 'error',
              message: '!'
            });
          }
          //
          this.getGoodsList();
          this.$message({
            type: 'success',
            message: '!'
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    //
    addGoods() {
      this.$router.push('/goods/add');
    },
    //
    editGoods(info) {
      console.log(info);
      this.$router.push({ path: '/goods/edit', query: { goods_id: info.goods_id } });
    }
  }
};
</script>

<style lang="less" scoped></style>
