<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-row>
        <el-col><el-button type="primary" @click="addCate"></el-button></el-col>
      </el-row>

      <!--  -->
      <zk-table :data="cateList" :columns="columns" :selection-type="false" :expand-type="false" border show-index>
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i style="color:springgreen" class="el-icon-success" v-if="scope.row.cat_deleted === false"></i>
          <i style="color: red;" class="el-icon-error" v-else></i>
        </template>
        <!--  -->
        <template slot="level" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="editCate(scope.row)"></el-button>
          <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
        </template>
      </zk-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 10, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="addCateDialogVisible" width="50%" @close="addCateDialogClose">
      <el-form :model="addCateForm" :rules="addCateRules" ref="addCateRef" label-width="100px">
        <el-form-item label="" prop="cat_name"><el-input v-model="addCateForm.cat_name"></el-input></el-form-item>
        <el-form-item label="">
          <el-cascader
            expand-trigger="hover"
            v-model="parentArray"
            :options="parentCateList"
            :props="parentCateProps"
            @change="parentCateChange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="subAddCateForm"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editCateDialogVisible" width="50%" @close="editCateClose">
      <el-form :model="editCateForm" :rules="editCateRules" ref="editCateRef" label-width="100px">
        <el-form-item label="" prop="cat_name"><el-input v-model="editCateForm.cat_name"></el-input></el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="subEditCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //
      cateList: [],
      //
      queryInfo: {
        //123 
        type: 3,
        //
        pagenum: 1,
        //
        pagesize: 2
      },
      //
      total: 0,
      //
      columns: [
        {
          label: '',
          prop: 'cat_name',
          align: 'center',
          headerAlign: 'center'
        },
        {
          label: '',
          prop: 'cat_deleted',
          align: 'center',
          headerAlign: 'center',
          type: 'template',
          template: 'isOk'
        },
        {
          label: '',
          align: 'center',
          headerAlign: 'center',
          type: 'template',
          template: 'level'
        },
        {
          label: '',
          align: 'center',
          headerAlign: 'center',
          type: 'template',
          template: 'opt'
        }
      ],
      //
      parentCateList: [],
      //
      parentArray: [],
      //
      parentCateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //
      addCateForm: {
        //ID,0
        cat_pid: 0,
        //
        cat_name: '',
        //,0
        cat_level: 0
      },
      //
      addCateRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      //
      addCateDialogVisible: false,
      //
      editCateDialogVisible: false,
      //Form
      editCateForm: {},
      //form
      editCateRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  created() {
    this.getCateList();
  },
  methods: {
    //
    async getCateList() {
      const { data: res } = await this.$http.get('categories', { params: this.queryInfo });
      if (res.meta.status !== 200) return this.$message.error('');
      this.cateList = res.data.result;
      this.total = res.data.total;
    },
    //
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.getCateList();
    },
    //
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.getCateList();
    },
    //
    addCate() {
      this.getParentCateList();
      //
      this.addCateDialogVisible = true;
    },
    //
    addCateDialogClose() {
      //
      this.parentArray = [];
      this.addCateForm.cat_pid = 0;
      this.addCateForm.cat_level = 0;
      //
      this.$refs.addCateRef.resetFields();
    },
    //
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } });
      if (res.meta.status !== 200) return this.$message.error('!');
      this.parentCateList = res.data;
    },
    //ID
    parentCateChange() {
      //this.parentArray.length0,
      if (this.parentArray.length > 0) {
        this.addCateForm.cat_pid = this.parentArray[this.parentArray.length - 1];
        this.addCateForm.cat_level = this.parentArray.length;
      } else {
        this.addCateForm.cat_pid = 0;
        this.addCateForm.cat_level = 0;
      }
    },
    //
    subAddCateForm() {
      this.$refs.addCateRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post('categories', this.addCateForm);
        if (res.meta.status !== 201) return this.$message.error('!');
        this.$message.success('!');
        //
        this.getCateList();
        //
        this.addCateDialogVisible = false;
      });
    },
    //
    async editCate(info) {
      const { data: res } = await this.$http.get(`categories/${info.cat_id}`);
      if (res.meta.status !== 200) return this.$message.error('');
      this.editCateForm = res.data;
      //
      this.editCateDialogVisible = true;
    },
    //form
    subEditCate() {
      this.$refs.editCateRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.put(`categories/${this.editCateForm.cat_id}`, { cat_name: this.editCateForm.cat_name });
        if (res.meta.status !== 200) return this.$message.error('');
        this.$message.success('');
        //
        this.getCateList();
        //
        this.editCateDialogVisible = false;
      });
    },
    //
    editCateClose() {
      //
      this.$refs.editCateRef.resetFields();
    }
  }
};
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
